.carousel{overflow:hidden;position:relative}.carousel-dots{position:absolute;text-align:center;width:100%;bottom:5px}.carousel-dot{border:0;outline:0;background:#fff;opacity:.3;font-size:12px;width:1em;height:1em;border-radius:50%;margin:0 7px}.carousel-dot:focus,.carousel-dot:hover{opacity:.7}.carousel-dot.carousel-dot-active{opacity:1}